<template>
    <div :class="'mypage-container '+mobileClass">

        <div class="contents">
            <div class="from-box">

                <div class="sTitle"><span class="line">참가자 개인별 상세 내역</span></div>
                <div class="data-list-box">
                    <ul class="data-list">
                        <li v-for="(data,idx) in dataList" @click="getDataInfo(idx)">
                            <div class="list">
                                <div class="subject">{{ data.name }}</div>
                                <div class="email">{{ data.email }}</div>
                            </div>
                            <div class="list">
                                <div :class="'jumsu '+data.join">{{ data.joinText }}</div>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="button-row">
                    <v-btn
                            type="button"
                            class="green"
                            @click="excelDown()">엑셀로 다운로드
                    </v-btn>
                    <v-btn
                            type="button"
                            class="submit-button"
                            @click="goList()">목록으로
                    </v-btn>
                </div>
            </div>
            <table id="excelTable" border="1" style="width:100%;">
                <thead>
                <tr>
                    <th colspan="3" style="height:50px"> 참가자 개인별 상세 내역</th>
                </tr>
                </thead>
                <thead>
                <tr>
                    <th style="height:40px;width:100px">번호</th>
                    <th style="height:40px;width:200px">이름</th>
                    <th style="height:40px;width:200px">완료여부</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(item,idx) in dataList">
                    <td style="text-align:center;height:25px;">{{ idx + 1 }}</td>
                    <td style="text-align:center">{{ item.name }}</td>
                    <td style="text-align:center">{{ item.joinText }}</td>

                </tr>
                </tbody>
            </table>
        </div>

    </div>
</template>


<script>
    import JQ from 'jquery'
    import {mapGetters} from "vuex";
    import {
        API_URL_BROADCAST_LIVE_POLL_AFTER_WITH_USER_LIST
    } from "@/config/web/urls";
    import apiService from "@/config/web/apiService";

    export default {
        name: 'afterPollUser',
        data() {
            return {
                mobileView: false,
                mobileClass: '',
                parentId: '',
                id: '',
                dataList: [],
            }
        },
        created() {
            if (this.$isMobile()) {
                this.mobileView = true;
                this.mobileClass = 'mobile'
            }
            this.id = this.$route.params.id;
            this.parentId = this.getGno;
            this.getData();
        },
        computed: {
            ...mapGetters('broadcast', ["getGno"]),

        },
        methods: {
            getData() {
                const frm = {parentId: this.parentId.toString(), id: this.id}
                apiService.post(API_URL_BROADCAST_LIVE_POLL_AFTER_WITH_USER_LIST, frm, (ret) => {
                    let joinUser = {};
                    for (let data of ret.dataList) {
                        joinUser[data.uid] = data.id;
                    }
                    for (let user of ret.userList) {
                        if (joinUser[user.uid]) {
                            user.join = 'join';
                            user.joinText = '완료'
                            user.answerNo = joinUser[user.uid];
                        } else {
                            user.join = '';
                            user.joinText = '미완료'
                        }
                        this.dataList.push(user)
                    }

                });
            },
            getDataInfo(idx) {
                let item = this.dataList[idx];
                if (item.join == 'join') {
                    this.$router.push('/video/setting/afterPollUserReport/' + item.answerNo);
                }
            },
            goList() {
                this.$router.go(-1);
            },
            excelDown() {
                let tab_text = "<table border='1px'>";
                let exportTable = JQ('#excelTable').clone();
                tab_text = tab_text + exportTable.html();
                tab_text = tab_text + '</table>';

                const fileName = '참가자 개인별 상세내역.xlsx';
                const sheetName = '참가자 개인별 상세내역';

                let elt = document.createElement("div");
                elt.innerHTML = tab_text;
                document.body.appendChild(elt);

                const wb = window.XLSX.utils.book_new();
                const workSheet = window.XLSX.utils.table_to_sheet(elt.getElementsByTagName("TABLE")[0]);
                document.body.removeChild(elt);


                // step 3. workbook에 새로만든 워크시트에 이름을 주고 붙인다.
                window.XLSX.utils.book_append_sheet(wb, workSheet, sheetName);
                // step 4. 엑셀 파일 만들기
                const wbout = window.XLSX.write(wb, {bookType: 'xlsx', type: 'binary'});
                // step 5. 엑셀 파일 내보내기
                window.saveAs(new Blob([this.s2ab(wbout)], {type: "application/octet-stream"}), fileName);

            },
            s2ab(s) {
                const buf = new ArrayBuffer(s.length); //convert s to arrayBuffer
                const view = new Uint8Array(buf);  //create uint8array as viewer
                for (var i = 0; i < s.length; i++) view[i] = s.charCodeAt(i) & 0xFF; //convert to octet
                return buf;
            },


        },

    }
</script>
<style lang="scss" scoped>
    ul {
        padding: 0;

        li {

        }
    }

    .mypage-container {
        .data-list-box {
            border: solid 1px #DCDCDC;
            background: #F4F4F4;
            width: 1150px;
            height: 600px;
            overflow-y: auto;
            overflow-x: hidden;
            margin: 30px 0;
            padding: 50px;

            .data-list {
                width: 100%;
                display: flex;
                flex-wrap: wrap;

                li {
                    cursor: pointer;
                    width: 30.8%;
                    margin: 0 39px 39px 0;
                    height: 100px;
                    padding: 35px 20px 0 20px;
                    border-radius: 10px;
                    background: #fff;
                    border: solid 1px #dcdcdc;
                    position: relative;

                    &:nth-child(3n) {
                        margin-right: 0;
                    }

                    &:hover {
                        border: solid 1px #2D8CFF;
                    }

                    display: flex;

                    .list {
                        font-size: 9pt;
                        width: 50%;

                        .subject {
                            text-align: left;
                            color: #000;
                            font-size: 16pt;
                            font-weight: bold;
                        }

                        .jumsu {
                            text-align: right;
                            font-size: 16pt;
                            font-weight: bold;
                            color: #999;

                            &.join {
                                color: #E31717;
                            }
                        }

                        .email {
                            margin-top: 5px;
                            text-align: left;
                        }

                        .result {
                            margin-top: 5px;
                            text-align: right;
                            color: #000;
                            font-weight: bold;

                        }
                    }

                }

            }

        }

        .button-row {
            width: 630px;
            margin: 40px auto;

            button {
                width: 300px;
                border-radius: 30px;
                min-height: 60px;
                color: #fff;

                &.green {
                    margin-right: 30px;
                    background: #207245;
                    color: #fff;
                }

                &.submit-button {
                    background: #2D8CFF;
                }
            }
        }

        #excelTable {
            display: none;

            th, td {
                text-align: center;
            }
        }

        &.mobile {
            .data-list-box {
                width: 100%;
                height: auto;
                overflow-y: visible;
                overflow-x: hidden;
                margin: 30px 0;
                padding: 15px;

                .data-list {
                    width: 100%;
                    display: flex;
                    flex-wrap: wrap;
                    padding:0;
                    li {
                        -webkit-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                        -moz-box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                        box-shadow: 1px 1px 9px -1px rgba(71, 70, 71, 0.53);
                        width: 48%;
                        margin:0 4% 20px 0;
                        height: 100px;
                        padding:20px 10px;
                        border-radius: 10px;
                        position: relative;

                        &:nth-child(2n) {
                            margin-right: 0;
                        }

                        &:nth-child(3n) {
                            margin-right: 4%;
                        }

                        &.no-data {
                            margin-right: 0;
                        }

                        .list {
                            font-size: 9pt;
                            .subject {
                                font-size: 11pt;
                            }

                            .jumsu {
                                font-size: 12pt;

                            }

                            .email {
                                margin-top: 5px;
                                text-align: left;
                            }


                        }
                    }

                }


            }

            .button-row {
                width: 100%;
                margin: 40px auto;
                display: flex;
                justify-content: space-between;

                button {
                    width: 48%;
                    border-radius: 25px;
                    min-height: 50px;
                    color: #fff;

                    &.green {
                        margin-right: 0;
                        background: #207245;
                        color: #fff;
                    }

                    &.submit-button {
                        background: #2D8CFF;
                    }
                }
            }

        }
    }
</style>
